import React, { useState, useEffect } from "react";
import Cards from "../Components/Cards";
import { Link } from "react-router-dom";
import Loader from "../Components/Loader";
export default function Home() {
  const [data, getData] = useState([]);
  const [loading, setLoading] = useState(false);
  useEffect(() => {
    setLoading(true);
    const getCoronaInfo = async () => {
      fetch(`https://covid19.mathdro.id/api`)
        .then(response => response.json())
        .then(data =>
          {getData(prevArr => [...prevArr, data.confirmed.value]);
          getData(prevArr => [...prevArr, data.recovered.value]);
          getData(prevArr => [...prevArr, data.deaths.value]);
          setLoading(false);}
        )
        .catch(error => console.log("Error occured: " + error));
    };
    getCoronaInfo();
  }, []);
  if (loading) {
    return <Loader />;
  }
  return (
    <div id="home-container">
      <div className="grid-container">
        {data.map(item => (
          <Cards key={item.index} item={item} number={data.indexOf(item)} />
        ))}
      </div>
      <div className="button-container">
        <Link to="/corona-toll-react-app/tips">Safety tips</Link>
      </div>
    </div>
  );
}
