import React from 'react'

export default function Cards(props) {
    const numbers = ["Confirmed", "Recovered", "Deaths", "Safety Tips"];
    return (
        <div  key={props.id} className="grid-content cards-container">
            <p>{numbers[props.number]}</p>
            <p className="count">{props.item}</p>
        </div>
    )
}
