import React from 'react';
import {Link} from 'react-router-dom';

function Tips() {
    return (
        <div className="tips-container">
            <p><strong>Wash you hands with soap</strong> - Soap removes the fat that holds the virus and it's protiens together. Once the grease is removed, the virus disconnects from it's protien and dies.</p>
            <p><strong>Do not touch your face</strong> - The virus that is present on the surface of your hands could get into your nose, eyes and mouth.</p>
            <p><strong>Keep safe distance</strong> - The primary mode of transport of this virus is through droplets from the infected patient. So keep safe distance from people.</p>
            <p><strong>Stay at home</strong> - This helps both you and your community stop spreading the virus.</p>
            <div className="button-container">
            <Link to = "/corona-toll-react-app">Go back</Link>
            </div>
        </div>
    )
}

export default Tips
