import React from 'react';
import {Link} from 'react-router-dom';

function About() {
    return (
        <div className="about-container">
            <p>This web app was developed in React JS and it follows a particular design pattern called Neumorphism.</p>
            <p>The purpose of this app is to get real-time data on the deadly Novel Corona Virus codenamed COVID-19.</p>
            <p>This project is developed by Vignesh Narendran and the source code is available in this <a style={{textDecoration: 'underline'}}  href="https://github.com/vignesh-narendran/corona-toll">github repository</a></p>
            <div className="button-container">
            <Link to = "/corona-toll-react-app">Go back</Link>
            </div>
        </div>
    )
}

export default About
